:root {
  --Nsecbgclr: #222222;
  --cardcolor: #000000;
  --navunderlineclr:red;
  --underlinesmall: aqua;
  --newstitlehoverclr: #EF6029;
}

* {
  color: white;
  box-sizing: border-box;
  font-family: 'Abril Fatface', cursive;
  font-family: 'Arvo', serif;
  font-family: 'Dongle', sans-serif;
  font-family: 'Poppins', sans-serif;
  font-family: 'Quicksand', 'Helvetica' sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--Nsecbgclr);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav-item,.nav-link,.card, .card img{
  transition: all .5s;
}
.nav-item:hover, .nav-link:hover{
  border-bottom: 2px solid var(--navunderlineclr);
  /* box-shadow: 0px 2px 0px var(--navunderlineclr); */
}
#newssection{
  padding-top: 10px;
}
#newssection>h1 {
  margin: 50px 5px;
  padding: 10px 0px 2px 0px;
  font-weight: 700;
  position: relative;
}
#newssection h1::after{
  content:"";
  position: absolute;
  width: 70px;
  height: 5px;
  background-color:var(--underlinesmall) ;
  bottom: -12px;
  left: 47%;
}

.card {
  padding: 2px;
  width: 25rem;
  overflow: hidden;
  background-color: var(--cardcolor);
  cursor: pointer;
  border-radius: 10px;
}

.card:hover {
  transform: translateY(-10px);
 box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.47);
}
.card:hover .card-title{
  color: var(--newstitlehoverclr);
}

.card img {
  height: 220px;
  min-height: 179px;
  border-radius: 10px 10px 0px 0px;
}

.card img:hover {
  transform: scale(1.1);
}
.card-body h6{position: relative;}

.card-body h6::after{
  content: "";
  position: absolute;
  width: 30px;
  height: 3px;
  left: 0;
  bottom:-5px;
  background-color:var(--underlinesmall);
}
.card-body .btn{position: relative;}
.card-body .btn::before{
  content: "Go";
  width: 92%;
  height: 100%;
  position: absolute;
  padding: 4px;
  left: 0px;
  bottom: 0px;
  transform: translateX(-80px);
  transition: all .5s;
  background: #f12711;
background: -webkit-linear-gradient(to right, #f5af19, #f12711); 
background: linear-gradient(to right, #f5af19, #f12711); 
}
.card-body .btn:hover::before{
  transform: translateX(0px);
  border-radius: 0px 15px 15px 0px;
}
.newschannel{
  position: absolute;
  right: 10px;
 top: 10px;
}
.loadingpage {
  text-align: center;
}
